:root {
  --d-gray: hsl(0, 0%, 23%);
  --l-gray: hsl(0, 0%, 59%);
  --white: hsl(0, 100%, 100%);
  --black: hsl(0, 100%, 0%);
}

:root {
  --font-rubik: "Rubik", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-rubik);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.app {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding: 1.5rem;
}
.app > * {
  margin-bottom: 1.5rem;
}
.app .title {
  align-self: center;
  font-size: 1.6rem;
  margin-top: 0.3rem;
  margin-bottom: 1.8rem;
  color: var(--white);
}
.app .search {
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
}
.app .search__box {
  height: 3rem;
  width: 100%;
  padding: 1.8rem 1.5rem;
  font-size: 1.2rem;
}
.app .search__btn {
  position: absolute;
  right: 0;
  height: 100%;
  background-color: var(--black);
  border: none;
  width: 18%;
  color: var(--white);
  font-weight: 700;
}
.app .info {
  padding: 1rem;
  background: var(--white);
  border-radius: 1.3rem;
}
.app .info__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0.6rem;
}
.app .info__wrapper > * {
  margin-bottom: 1rem;
}
.app .info__label {
  color: var(--l-gray);
  font-weight: 700;
  font-size: 0.6rem;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  margin-bottom: 0.5rem;
}
.app .info__item {
  color: var(--d-gray);
  font-weight: 700;
  font-size: 1.2rem;
}
.app .bg {
  position: absolute;
  scale: 1.3;
  z-index: -99;
  top: -1.25rem;
}